.App-header {
  height: 150px;
  padding: 20px;
}

.App-title {
  text-align: center;
  font-size: 2em;
  font-family: F25_Executive;
  /*font-variant: small-caps;*/
}
.AnnonceTMP {
    text-align: center;
}

.AnnonceTMP::after {
    content: "";
    background: url(/static/media/AnnonceTMP_background.9168de88.jpg);
    opacity: 0.35;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;   
  }

.AnnonceTMP > * {
    padding: 10px;
    font-size: 2em;
}

.AnnonceH3 {
    padding-top: 50px !important;
    font-weight: bold !important;
    font-size: 3em !important;
}

.AnnonceBtn {
    padding: 10px;
    font-weight: bold;
    text-decoration: none;
    color: white;
    background-color: #4267B2;
    border-radius: 10px;
}

.AnnonceBtn:hover{
    text-decoration: none;
    color: white;
    background-color: #3B5CA0;
}
.App-footer {
  font-size: 0.75em;
  font-style: italic;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 40px;
}

.App-footer > * {
  padding: 10px;
}

.Footer-left {
  position: fixed;
  left: 10px;
  bottom: 5px;
}

.Footer-right {
  position: fixed;
  bottom: 5px;
  right: 10px;
}
